<template>
    <div>
        <button type="button" class="btn btn-sm btn-style9  text-xs" @click="$router.go(-1)" style="position:fixed;top:10px;right:50px;z-index:1000;">{{$route.query.nama}}</button>
        <div v-if="modal" style="height:100vh;width:100vw;position:fixed;z-index:100;background:rgba(151,151,179,0.4);" class="d-flex justify-content-center align-items-center " @click="modal=false">
            <div style="width:100%">
            </div>
        </div>
        <div v-if="modal" style="position: fixed;top: 50%;left: 50%;transform: translateX(-50%) translateY(-50%);z-index:1000;">
            <div style="height:100px;" class="d-flex justify-content-center align-items-center ">
                <div style="" class="w-full">
                    <div class="p-4 bg-white rounded-lg shadow">
                        <p class="text-xs text-center font-bold p-3 uppercase">Pilih Kategori </p>
                        <div class="row">
                            <div class="col-4">
                                <button type="button" @click="pilihkategori='icon'" :class="`${pilihkategori=='icon'?'btn-dark':'btn-outline-dark'}`" class="btn btn-sm btn-block">Icon</button>
                            </div>
                            <div class="col-4">
                                <button type="button" @click="pilihkategori='gambar'" :class="`${pilihkategori=='gambar'?'btn-dark':'btn-outline-dark'}`" class="btn btn-sm  btn-block">Gambar</button>
                            </div>
                            <div class="col-4">
                                <button type="button" @click="pilihkategori='radius'" :class="`${pilihkategori=='radius'?'btn-dark':'btn-outline-dark'}`" class="btn btn-sm  btn-block">Radius</button>
                            </div>
                        </div>
                        <div style="width:70vw;height:80vh;overflow:scroll;">
                            <form class="text-sm" action="" @submit.prevent="submit">
                                <div v-if="pilihkategori=='radius'">
                                    <div class="row justify-content-center">
                                        <div class="col-sm-5">
                                             <p>Wilayah/Kecamatan : </p>
                                             <v-select :options="wilayah" label="kecamatan" v-model="vdata.wilayah" :reduce="e => e.id"></v-select>
                                            <div class="sm-form ">
                                                <label class="p-0 mt-2" for="title">Title : </label>
                                                <input required type="text" id="title" name="title" class="form-control form-control-sm" placeholder="title" v-model="vdata.title" >
                                            </div>
                                            <p class="text-sm">Deskripsi : </p>
                                            <div class="sm-form">
                                            <textarea
                                            type="text"
                                            id="deskripsi"
                                            name="deskripsi"
                                            rows="2"
                                            placeholder="deskripsi..."
                                            class="form-control md-textarea"
                                            v-model="vdata.deskripsi"
                                            ></textarea>
                                            </div>
                                            <div class="row">
                                                <div class="col-6">
                                                    <div class="sm-form ">
                                                        <label class="text-xs mt-1 p-0" for="lat">Lat : </label>
                                                        <input type="text" id="lat" name="lat" class="form-control form-control-sm" placeholder="lat" v-model="vdata.lat" >
                                                    </div>
                                                </div>
                                                <div class="col-6">
                                                    <div class="sm-form ">
                                                        <label class="text-xs mt-1 p-0" for="lng">Lng : </label>
                                                        <input type="text" id="lng" name="lng" class="form-control form-control-sm" placeholder="lng" v-model="vdata.lng" >
                                                    </div>
                                                </div>
                                            </div>
                                            <button type="button"  class="btn btn-sm btn-dark float-right " @click="gantiPosisi"><span class="typcn typcn-arrow-repeat"></span></button>
                                            <p class="text-sm">Type Popup : </p>
                                            <input type="radio" id="click" value="click" v-model="vdata.popup">
                                            <label for="click">click</label>
                                            <input type="radio" class="ml-2" id="hover" value="hover" v-model="vdata.popup">
                                            <label for="hover">hover</label>
                                        </div>
                                        <div class="col-sm-5">
                                            <div class="sm-form ">
                                                <label class="p-0 mt-2" for="radius">Radius lingkaran/area : </label>
                                                <p class="text-xs text-red-300">per Meter/M</p>
                                                <input required type="text" id="radius" style="width:50%;" name="radius" class="form-control form-control-sm" placeholder="ex: 100" v-model="vdata.radius" >
                                            </div>
                                            <div class="sm-form ">
                                                <label for="color">Warna/color : </label>
                                                <input required type="color" id="color" name="color" class="form-control form-control-sm" placeholder="color" v-model="vdata.color" >
                                            </div>
                                            <div class="sm-form ">
                                                <label for="opacity">Opacity (seberapa transparent) : </label>
                                                <input required type="text" id="opacity" name="opacity" class="form-control form-control-sm" placeholder="ex : 0.0 - 1.0" v-model="vdata.opacity" >
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div v-else class="sm-form ">
                                    <div class="row justify-content-center">
                                        <div class="col-sm-5">
                                           
                                            <div class="sm-form ">
                                                <label class="p-0 mt-2" for="title">Title : </label>
                                                <input required type="text" id="title" name="title" class="form-control form-control-sm" placeholder="title" v-model="vdata.title" >
                                            </div>
                                            <p class="text-sm">Deskripsi : </p>
                                            <div class="sm-form">
                                            <textarea
                                            type="text"
                                            id="deskripsi"
                                            name="deskripsi"
                                            rows="2"
                                            placeholder="deskripsi..."
                                            class="form-control md-textarea"
                                            v-model="vdata.deskripsi"
                                            ></textarea>
                                            </div>
                                            <div class="row">
                                                <div class="col-6">
                                                    <div class="sm-form ">
                                                        <label class="text-xs mt-1 p-0" for="lat">Lat : </label>
                                                        <input type="text" id="lat" name="lat" class="form-control form-control-sm" placeholder="lat" v-model="vdata.lat" >
                                                    </div>
                                                </div>
                                                <div class="col-6">
                                                    <div class="sm-form ">
                                                        <label class="text-xs mt-1 p-0" for="lng">Lng : </label>
                                                        <input type="text" id="lng" name="lng" class="form-control form-control-sm" placeholder="lng" v-model="vdata.lng" >
                                                    </div>
                                                </div>
                                            </div>
                                            <button type="button"  class="btn btn-sm btn-dark float-right " @click="gantiPosisi"><span class="typcn typcn-arrow-repeat"></span></button>
                                            <p class="text-sm">Type Popup : </p>
                                            <input type="radio" id="click" value="click" v-model="vdata.popup">
                                            <label for="click">click</label>
                                            <input type="radio" class="ml-2" id="hover" value="hover" v-model="vdata.popup">
                                            <label for="hover">hover</label>
                                        </div>
                                        <div class="col-sm-5">
                                            <div v-if="pilihkategori=='icon'">
                                                <label for="file" class="p-0 mt-2">Icon Link/Upload: </label>
                                                <input  type="file" id="filenya" name="file" class="form-control form-control-sm" placeholder="file" @change="upload" >
                                                <input required type="text" id="icon" name="icon" class="form-control form-control-sm" placeholder="icon" v-model="vdata.icon" >
                                            </div>
                                            <label for="file" class="p-0 mt-2">Gambar Link/Upload: </label>
                                            <p class="text-xs text-red-500" v-if="pilihkategori=='icon'">Gambar akan berada di deskripsi saat icon di klik </p>
                                            <input  type="file" id="filenya2" name="file" class="form-control form-control-sm" placeholder="file" @change="upload2" >
                                            <input  type="text" id="gambar" name="gambar" class="form-control form-control-sm" placeholder="gambar" v-model="vdata.gambar" >
                                            
                                        </div>
                                    </div>
                                </div>
                               <!-- BUTTON PART -->
                                <div class="mt-3 p-3">
                                    <button
                                    type="submit"
                                    class="btn btn-sm"
                                    :class="
                                        btn == 'tambah'
                                        ? 'btn-primary'
                                        : btn == 'update'
                                        ? 'btn-success'
                                        : 'btn-danger'
                                    "
                                    >
                                    <span class="typcn typcn-info"></span>  Proses {{ btn }}
                                    </button>
                                    <div class="float-right">
                                    <button
                                        type="button"
                                        v-show="btn!='tambah'"
                                        @click="
                                        btn = 'tambah';
                                        vdata = {};
                                        "
                                        class="btn btn-sm btn-dark"
                                    >
                                        <span class="typcn typcn-edit"></span> Tambah
                                    </button>
                                    <button
                                        type="button"
                                        @click="btn = 'delete'"
                                        v-show="btn!='tambah'"
                                        class="ml-3 btn btn-sm btn-danger"
                                    >
                                        <span class="typcn typcn-delete"></span> Delete
                                    </button>
                                    </div>
                                </div>
                                <!-- END BUTTON PART -->
                            </form>
                             <!-- TABLE PART -->
                            <div v-if="datanya.length>0" class="p-3">
                            <Btables :tables="td"  :cari="['']" :hide="['id','wilayah','id_peta','deskripsi','icon','gambar','radius','color','opacity']" @selected="ambil" ref="btable" />
                            </div>
                            <!-- END TABLE PART -->
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div v-if="ready">
            <div id="mapid" style="height:100vh;">
                
            </div>
        </div>
    </div>
</template>
<script>
// import FreeDraw from 'leaflet-freedraw';
import autophp from "@/plugins/autophp_gis.js"; // ganti source nya
import Btables from "@/components/Btables.vue"; //component btable nya
let sdb = new autophp();
import axios from 'axios'
let map;
export default{
    components:{
     Btables,
    },
    data(){
        return{
            modal:false,
            selected: false,
            ready:false,
            vdata: {
                opacity:'0.6',
                radius:'100'
            },
            btn: "tambah",
            wilayah:[],
            layers:[],
            datanya:[],
            posisi:[],
            listGeojson:[],
            pilihkategori:'icon',
            iconSize:30,
        }
    },
    computed:{
        td(){
            let that=this;
            let data=this.datanya
            data=data.filter(e=>{
                if(e.kategori==that.pilihkategori){
                    return e
                }
            })
            return data
        },
        dynamicSize() {
            return [this.iconSize, this.iconSize * 1.15];
        },
            dynamicAnchor() {
            return [this.iconSize / 2, this.iconSize * 1.15];
        }
    },
    
    mounted() {
        let that=this;
        this.getWilayah();
        this.loadmap()
    },
    methods: {
        getWilayah(){
            let that=this;
            sdb.collection("app_gis_wilayah_balangan").doc().select(`select * from app_gis_wilayah_balangan`).then(res => {
                this.wilayah=res;
                console.log('wilayah',res)
                this.$forceUpdate();
            });
             sdb.collection("app_gis_geojson").doc().get("*","order by id").then(res => {
                that.listGeojson=res;
                console.log('res',res)
                // let leftjoin = _.map(that.listMarker, function(obj) {
                //     return _.assign(
                //         obj,
                //         _.find(that.listPeta, {
                //         id: obj.id_peta
                //         })
                //     );
                // });
                // console.log('leftjoin',leftjoin)
                that.$forceUpdate();
            });
        },
        loadgeojson(item){
            let that=this
            this.listGeojson.forEach(e=>{
                axios.get(e.geojson).then(res=>{
                    if ( map.hasLayer(this.layers['geojson'+e.id]) ){  // if the specified layer is visible on the map, it needs to be removed
                        map.removeLayer(this.layers['geojson'+e.id])
                        this.layers['geojson'+e.id+'on']=false
                        return
                    }
                    console.log('that.laye',that.layers)
                    this.layers['geojson'+e.id]=L.geoJSON(res.data).addTo(map);
                    this.layers['geojson'+e.id+'on']=true
                    //   if(res.data.features[0].geometry.type=='Polygon'){
                        //     map.panTo(new L.LatLng(res.data.features[0].geometry.coordinates[0][0][1],res.data.features[0].geometry.coordinates[0][0][0]))
                    //   }else{
                        //     map.panTo(new L.LatLng(res.data.features[0].geometry.coordinates[0][1],res.data.features[0].geometry.coordinates[0][0]))
                    //   }
                    this.loadGeojsonLayer(this.layers['geojson'+e.id])
                    this.loading = false;
                    this.$forceUpdate();
                })
            })
        },
        loadmap(){
            let that=this;
            this.getData();
            this.ready=false;
            map=null;
            setTimeout(() => {
                this.ready=true;
                setTimeout(() => {
                    map = new L.Map('mapid');
                    // const freeDraw = new FreeDraw();
                    map.setView([ -2.3662494, 115.4641079], 10);
                    // L.tileLayer('https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token=pk.eyJ1IjoicnVkeXNrYSIsImEiOiJja3B1cGY0OGwxOTlxMm5xdnJ6dTU3dXZiIn0.vqqfQlT3-d3KF9KNzemANg', {
                    L.tileLayer('http://{s}.google.com/vt/lyrs=m&x={x}&y={y}&z={z}',{
                        maxZoom: 20,
                        subdomains:['mt0','mt1','mt2','mt3']
                        }).addTo(map);
                    map.on('click',function(e){
                        console.log(e)
                        that.posisi[0]=e.latlng.lat
                        that.posisi[1]=e.latlng.lng
                        
                        that.modal=true;
                        that.$forceUpdate();
                    })
                    that.loadgeojson();
                    if(that.datanya.length>0){
                    that.datanya.forEach(e=>{
                    // =================================================================
                        if(e.kategori=='icon'){
                            var greenIcon = L.icon({
                                iconUrl: e.icon,
                                iconSize:     [28, 75], // size of the icon
                                iconAnchor:   [12, 54], // point of the icon which will correspond to marker's location
                                popupAnchor:  [-3, -26] // point from which the popup should open relative to the iconAnchor
                            });
                            if(e.popup=='click'){
                                L.marker([e.lat, e.lng],{icon:greenIcon}).bindPopup(`
                                <div class="text-xs">
                                    <div class="font-bold uppercase">${e.title}</div>
                                    <div class="text-justify">${e.deskripsi}</div>
                                    ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                                </div>
                                `, {maxWidth: 500}).addTo(map)
                            }else{
                                L.marker([e.lat, e.lng],{icon:greenIcon}).bindTooltip(`
                                <div class="text-xs">
                                    <div class="font-bold uppercase">${e.title}</div>
                                    <div class="text-justify">${e.deskripsi}</div>
                                    ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                                </div>
                                `,{ permanent: false, sticky: true }).addTo(map)
                            }
                    // =================================================================
                        }else if(e.kategori=='gambar'){
                            var greenIcon = L.icon({
                                iconUrl: e.gambar,
                                iconSize:     [48, 45], // size of the icon
                                iconAnchor:   [12, 54], // point of the icon which will correspond to marker's location
                                popupAnchor:  [-3, -26] // point from which the popup should open relative to the iconAnchor
                            });
                            if(e.popup=='click'){
                                L.marker([e.lat, e.lng],{icon:greenIcon}).bindPopup(`
                                <div class="text-xs">
                                    <div class="font-bold uppercase">${e.title}</div>
                                    <div class="text-justify">${e.deskripsi}</div>
                                    ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                                </div>
                                `, {maxWidth: 500}).addTo(map)
                            }else{
                                L.marker([e.lat, e.lng],{icon:greenIcon}).bindTooltip(`
                                <div class="text-xs">
                                    <div class="font-bold uppercase">${e.title}</div>
                                    <div class="text-justify">${e.deskripsi}</div>
                                    ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                                </div>
                                `,{ permanent: false, sticky: true }).addTo(map)
                            }
                    // =================================================================
                        }else if(e.kategori=='radius'){
                            if(e.popup=='click'){
                                L.circle([e.lat, e.lng], {
                                    color: "black",
                                    fillColor: e.color,
                                    fillOpacity: e.opacity,
                                    radius: e.radius
                                }).bindPopup(`
                                <div class="text-xs">
                                    <div class="font-bold uppercase">${e.title}</div>
                                    <div class="text-justify">${e.deskripsi}</div>
                                </div>
                                `, {maxWidth: 500}).addTo(map);
                            }else{
                                L.circle([e.lat, e.lng], {
                                    color: "black",
                                    fillColor: e.color,
                                    fillOpacity: e.opacity,
                                    radius: e.radius
                                }).bindTooltip(`
                            <div class="text-xs">
                                <div class="font-bold uppercase">${e.title}</div>
                                <div class="text-justify">${e.deskripsi}</div>
                            </div>
                            `,{ permanent: false, sticky: true }).addTo(map);
                            }
                        }
                // =================================================================
                    })
                    }
                  L.control.ruler().addTo(map);
                }, 2000);
                
            }, 1000);
        },
        ambil(){
            
        },
        gantiPosisi(){
            this.vdata.lat=this.posisi[0];
            this.vdata.lng=this.posisi[1];
            this.$forceUpdate();
        },
        getData(){
                sdb.collection("app_gis_marker").doc().select(`
                select *,tb1.id as id
                 from app_gis_marker tb1 left join app_gis_wilayah_balangan tb2 on tb2.id=tb1.wilayah where tb1.id_peta='${this.$route.query.id}'`).then(res => {
                    console.log('hasil',res)
                    this.datanya=res;
                    this.$forceUpdate();
                });
            },
        ambil(data) {
            this.vdata = data;
            this.pilihkategori=data.kategori
            this.btn = "update";
            this.$forceUpdate();
        },
        submit() {
            let that=this
            that.vdata.kategori=that.pilihkategori;
            that.vdata.id_peta=this.$route.query.id
            if (confirm("Apakah yakin proses dilanjutkan ?")) {
                if (this.btn == "tambah") {
                //idnya letak di vdata
                delete this.vdata.id;
                sdb
                    .collection("app_gis_marker")
                    .doc()
                    .set(this.vdata)
                    .then((res) => {
                    that.getData();
                    // that.$nuxt.$emit('getData')

                    });
                } else if (this.btn == "update") {
                sdb
                    .collection("app_gis_marker")
                    .doc()
                    .set(this.vdata)
                    .then((res) => {
                    that.getData();
                    // that.$nuxt.$emit('getData')
                    });
                } else if (this.btn == "delete") {
                    console.log(this.vdata)
                sdb
                    .collection("app_gis_marker")
                    .doc(this.vdata.id)
                    .delete()
                    .then((res) => {
                    that.getData();
                    // that.$nuxt.$emit('getData')
                    });
                }
                // this.$nuxt.$emit('getData','ok')
                this.loadmap()
            }
        },
        upload(){
            let file = document.querySelector("#filenya"); // berikan id pada input file
            sdb.collection('tbuser').upload(file).then(res=>{
                console.log(res)
            })
        },
        upload2(){
            let file = document.querySelector("#filenya2"); // berikan id pada input file
            sdb.collection('tbuser').upload(file).then(res=>{
                console.log(res)
            })
        }
    },
     metaInfo: {
        title: 'Add Marker dynamic',
        meta: [
         { charset: 'utf-8' },
         { name: 'viewport', content: 'width=device-width, initial-scale=1' }
        ],
        link: [
         { rel: 'stylesheet', href: 'https://unpkg.com/leaflet@1.7.1/dist/leaflet.css', integrity:'sha512-xodZBNTC5n17Xt2atTPuE1HxjVMSvLVW9ocqUKLsCC5CXdbqCmblAshOMAS6/keqq/sMZMZ19scR4PsZChSR7A==',crossorigin:'' },
         { rel: 'stylesheet', href: 'https://cdn.jsdelivr.net/gh/gokertanrisever/leaflet-ruler@master/src/leaflet-ruler.css', integrity:'sha384-P9DABSdtEY/XDbEInD3q+PlL+BjqPCXGcF8EkhtKSfSTr/dS5PBKa9+/PMkW2xsY',crossorigin:'anonymous' },
        ],
        script: [
            { src: 'https://unpkg.com/leaflet@1.7.1/dist/leaflet.js', integrity: "sha512-XQoYMqMTK8LvdxXYG3nZ448hOEQiglfqkJs1NOQV44cWnUrBc8PkAOcXy20w0vlaXaVUearIOBhiXZ5V3ynxwA==", crossorigin: ''},
            { src: 'https://cdn.jsdelivr.net/gh/gokertanrisever/leaflet-ruler@master/src/leaflet-ruler.js', integrity: "sha384-N2S8y7hRzXUPiepaSiUvBH1ZZ7Tc/ZfchhbPdvOE5v3aBBCIepq9l+dBJPFdo1ZJ", crossorigin: 'anonymous'},
        ],
    },
}
</script>